import React from "react";

const Navigation = (props) => {
  const isCompetence = () => props.active === "competences";
  const isExperience = () => props.active === "experiences";
  const isFormation = () => props.active === "formations";
  return (
    <div className="navigation">
      <ul className="navContent">
        <li
          className={`competences ${isCompetence() ? "isActive" : ""}`}
          onClick={() => props.handleNavClick("competences")}
        >
          Compétences
        </li>
        <li
          className={`experiences ${isExperience() ? "isActive" : ""}`}
          onClick={() => props.handleNavClick("experiences")}
        >
          Expériences
        </li>
        <li
          className={`formations ${isFormation() ? "isActive" : ""}`}
          onClick={() => props.handleNavClick("formations")}
        >
          Formations
        </li>
      </ul>
    </div>
  );
};

export default Navigation;
