import React, { useState } from "react";
import Affichages from "../components/Affichages";
import Header from "../components/Header";
import Navigation from "../components/Navigation";

const Cv = () => {
  const [page, setPage] = useState("competences");

  const handleNavClick = (val) => {
    setPage(val);
  };

  return (
    <div className="content">
      <Header />
      <Navigation active={page} handleNavClick={handleNavClick} />
      <Affichages page={page} />
    </div>
  );
};

export default Cv;
