import React from "react";
import data from "../data/data";

const Header = () => {
  return (
    <div className="header">
      <div className="title">
        <h2>
          <span className="color1">S</span>
          <span className="color2">y</span>
          <span className="color3">l</span>
          <span className="color2">v</span>
          <span className="color1">a</span>
          <span className="color3">i</span>
          <span className="color1">n </span>

          <span className="color2">C</span>
          <span className="color3">a</span>
          <span className="color1">p</span>
          <span className="color3">u</span>
          <span className="color2">s</span>
        </h2>
        <h3>{data.informations.poste}</h3>
      </div>
      <div className="info">
        <p>{data.informations.adresse}</p>
        <p>{data.informations.email}</p>
        <p>{data.informations.permis}</p>
      </div>
    </div>
  );
};

export default Header;
